<template>
  <div>
    <el-row></el-row>
    <el-row>
      <el-col :span="24">
        <ve-pie :data="chartData" :settings="chartSettings"></ve-pie>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
      this.chartSettings = {
        dimension: 'section',
        metrics: 'counts',
        roseType: 'radius'
      }
      return {
        chartData: {
          columns: ['section','counts'],
          rows: []
        }
      }
    },
  methods: {
    getData() {
      var api =
        "http://localhost:8888/paper/count/point/" + this.$route.params.paperID
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.chartData.rows = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

